import { Component } from '@angular/core';
import { NavController, NavParams } from 'ionic-angular';
import { FileTransfer, FileUploadOptions, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';

@Component({
  templateUrl: 'certificato.html'
})
export class CertificatoPage {
public item: any;
  constructor(public navCtrl: NavController,public transfer: FileTransferObject, private file: File, public params:NavParams) {
      this.item = params.get('item');
      this.download();
  }
  

  download() {
    const url = this.item.url;
    this.transfer.download(url, this.file.dataDirectory + 'file.pdf').then((entry) => {
      console.log('download complete: ' + entry.toURL());
    }, (error) => {
      // handle error
    });
  }
}



